import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import db from 'libs/db';
import AdminServices from 'services/api/AdminServices';

const VideoPlayer = (props) => {
    const isWhatAreMintboxes = props.location.state.id === 1

    const [videosArray, setVideoArray] = React.useState(props.location.state.allVideos || {});
    const [currentVideo, setCurrentVideo] = React.useState(props.location.state.selectedVideo || 0);
    const [loading, setLoading] = React.useState(true);

    React.useEffect(async () => {
        if (isWhatAreMintboxes) {
            await getDashboardData().then(()=>{
                setLoading(false)
            })
        } else {
            setLoading(false)
        }

    }, [])

    const getDashboardData = async () => {
        await db.get(db.model.dashboard).then(async (p) => {
            if (p === null) {
                let res = await AdminServices.getDashboardData().then(res => res);
                if (res.type === true) {
                    console.log("Get Dashboard API: ", res.data)
                    await db.set(db.model.dashboard, JSON.stringify(res.data))
                    setVideoArray(res.data[0].dashboardSection[1])
                }
            } else {
                setVideoArray(p[0].dashboardSection[1])
            }
        })
    }

    React.useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'auto' });
    }, [currentVideo])

    return (
        <div className='mainContainer'>
            <MainHeader
                header={isWhatAreMintboxes?'What are mintboxes?': videosArray?.sectionHeading}
                navigation={props.history}
            />
            <div className="dataContainer px-0">
                {
                    loading ?
                        <ActivityLoaderMintbox />
                        :
                        <div className='sticky-top' style={{background:cssVar.getPropertyValue('--white'),top:'56px'}}>
                            <iframe width="100%" height="300" src={`https://www.youtube.com/embed/${videosArray?.sectionChildren?.[currentVideo]?.videoId}`} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                            <div className='header2dark' style={{ padding: '4%', borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
                                {videosArray?.sectionChildren?.[currentVideo]?.videoTitle}
                            </div>
                        </div>
                }
                {
                    videosArray?.sectionChildren?.map((v, i) => {
                        if (i === currentVideo) {
                            return <></>
                        }
                        else {
                            return <div style={{ display: 'flex', gap: '4%', padding: '4%', borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }} onClick={() => { setCurrentVideo(i) }}>
                                <div style={{ flex: 2 }}>
                                    <img src={`https://img.youtube.com/vi/${v.videoId}/maxresdefault.jpg`}
                                        style={{
                                            width: "100%",
                                            borderRadius: '8px',
                                        }} />
                                </div>
                                <div className='header2dark' style={{ flex: 3 }}>
                                    {v.videoTitle}
                                </div>
                            </div>
                        }
                    })
                }
            </div>

        </div>
    )
}

export default VideoPlayer
